import { createStore, applyMiddleware, combineReducers, compose } from "redux";
import thunk from "redux-thunk";
import { routerMiddleware, connectRouter } from 'connected-react-router'
import { createBrowserHistory } from "history";
import home from "../modules/home/HomeReducer";
import MyRegistryReducers from "../modules/my-registry/MyRegistryReducer";
import ViewRegistryReducers from "modules/view-registry/ViewRegistryReducer";
import registryInfo from "../modules/registry/RegistryReducer";
import PersonaliseCheckListReducer from "../modules/personalise-checklist/PersonaliseCheckListReducer";
import Onboarding from "../modules/onboarding/OnboardingReducer";

export const history = createBrowserHistory();

const rootReducer = combineReducers({
  home,
  registry: registryInfo,
  myRegistry: MyRegistryReducers,
  viewRegistry: ViewRegistryReducers,
  personaliseCheckList: PersonaliseCheckListReducer,
  onboarding: Onboarding,
  router: connectRouter(history)
})

const middlewares = [routerMiddleware(history), thunk];

const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose; // eslint-disable-line

export const store = createStore(
  rootReducer,
  composeEnhancer(applyMiddleware(...middlewares))
);
