import {PERSONALISE_CHECKLIST_ACTION_TYPES} from "./PersonaliseCheckListActions";
import {combineReducers} from "redux";

// Functions
const incrementProductQuantity = (state, payload) => {
  let {
    id,
    quantity
  } = payload;

  let stateInc = [...state];
  let newState = [];
  for (let category of stateInc) {
    let newSubCategory = []
    for (let subCategory of category.subCategories) {
      if (subCategory.id === id) {
        subCategory.totalItemsCount += quantity;
      }
      newSubCategory.push(subCategory)
    }
    category.subCategories = newSubCategory
    newState.push(category)
  }
  return newState;
}

const decrementProductQuantity = (state, payload) => {
  let {
    id,
    quantity
  } = payload;

  let stateInc = [...state];
  let newState = [];
  for (let category of stateInc) {
    let newSubCategory = []
    for (let subCategory of category.subCategories) {
      if (subCategory.id === id && subCategory.totalItemsCount > 1) {
        subCategory.totalItemsCount -= quantity;
      }
      newSubCategory.push(subCategory)
    }
    category.subCategories = newSubCategory
    newState.push(category)
  }
  return newState;
}

const setIsAddedCheckList = (state, payload) => {
  let {
    id,
    isAdded
  } = payload;

  let stateInc = [...state];
  let newState = [];
  for (let category of stateInc) {
    let newSubCategory = []
    for (let subCategory of category.subCategories) {
      if (subCategory.id === id) {
        subCategory.isAdded = isAdded;
      }
      newSubCategory.push(subCategory)
    }
    category.subCategories = newSubCategory
    newState.push(category)
  }
  return newState;
}

// Reducers
const editedPersonaliseCheckList = (state = [], action) => {
  switch (action.type) {
    case PERSONALISE_CHECKLIST_ACTION_TYPES.GET_ALL_CHECKLIST_ITEMS:
      return [...action.payload];
    case PERSONALISE_CHECKLIST_ACTION_TYPES.INCREMENT_CHECKLIST_QUANTITY:
      return incrementProductQuantity(state, action.payload);
    case PERSONALISE_CHECKLIST_ACTION_TYPES.DECREMENT_CHECKLIST_QUANTITY:
      return decrementProductQuantity(state, action.payload);
      case PERSONALISE_CHECKLIST_ACTION_TYPES.CHECKLIST_ADDED:
        return setIsAddedCheckList(state, action.payload);
    default:
      return state;
  }
}

const questions = (state = [], action) => {
  switch (action.type) {
    case PERSONALISE_CHECKLIST_ACTION_TYPES.PERSONALISE_ADD_QUESTION:
      return [...state, ...action.payload]
    default:
      return state
  }
}

const answers = (state = [], action) => {
  switch (action.type) {
    case PERSONALISE_CHECKLIST_ACTION_TYPES.PERSONALISE_ADD_ANSWER:
      return [...state, action.payload]
    case PERSONALISE_CHECKLIST_ACTION_TYPES.PERSONALISE_UPDATE_ANSWER:
      return [...action.payload]
    default:
      return state
  }
}



export default combineReducers({
  editedPersonaliseCheckList,
  questions,
  answers
})