import React from "react";
import "./TogglePills.scss";

const TogglePills = (props) => {
  return (
    <div
      className={["toggle-pills p-2 d-flex align-items-end", props.activeClass, props.className ? props.className : ""].join(" ")}
      onClick={props.onClick}
    >
      <i className={[props.iconClass, "mr-2"].join(" ")}/>
      <span className="text-sm">{props.text}</span>
    </div>
  )
}

export default TogglePills;