import {ApolloClient, HttpLink, InMemoryCache} from '@apollo/client';
import {onError} from '@apollo/client/link/error';
import {ApolloLink} from '@apollo/client/link/core/ApolloLink';
// import {API_URL} from '../utils/constants';

const cache = new InMemoryCache();


const errorLink = onError(({graphQLErrors, networkError}) => {
  if (graphQLErrors)
    graphQLErrors.map(({message, locations, path}) =>
      console.log(
        `[GraphQL error]: Message: ${message}, Location: ${locations}, Path: ${path}`,
        // TODO: Add pop-up alert error message for GraphQL error
      ),
    );
  if (networkError) {
    console.log(`[Network error]: ${networkError}`);
    // TODO: Add pop-up alert error message for network error
  }
});


const httpLink = new HttpLink({
//  TODO: Add dynamic api url
//  uri: API_URL,
    uri: 'http://127.0.0.1:8000/graphql',
});

const link = ApolloLink.from([errorLink, httpLink]);

export default new ApolloClient({
  link,
  cache,  
  
// TODO: Add store to header if any
//   headers: {
//     authorization: store.getState()?.auth?.token
//       ? `Bearer ${store.getState()?.auth?.token}`
//       : null,
//     language: AppString.getLanguage() ?? 'en',
//   },
});