import React from "react";
import "./CheckListItems.scss";
import { withTranslation } from "react-i18next";
import { withRouter } from "react-router";
import routes from "utils/routes";

class CheckList extends React.Component {

    gotoPersonalize = (event) => {
        event.preventDefault();
        this.props.history.push(routes.PERSONALIZE.HOME)
    }

    render() {
        return (
            <div className="mb-6">
                <div className="row check-list-header">
                    <div className="col d-flex align-items-baseline pb-1">
                        <p className="checklist-title mb-0">{this.props.t("checkList.title")}</p>
                        <a href="#" className="ml-2 text-sm checklist-personalize" onClick={(e) => this.gotoPersonalize(e)}>{this.props.t("checkList.personalize")}</a>
                    </div>
                </div>
                <div className="row">
                    <div className="col d-flex">
                        <div className="check-list-background">
                          <div className="check-list-progress mw-100" style={{width: `${this.props.percentage}%`}}/>
                        </div>
                    </div>
                </div>
            </div>

        );
    }
};

export default withTranslation()(withRouter(CheckList));