import {MY_REGISTRY_ACTION_TYPES} from "./MyRegistryActions";
import {combineReducers} from "redux";
import defaultData from "data/ReducerDefaultData.json";

// Functions
const incrementProductQuantity = (state, payload) => {
  let {
    id,
    quantity
  } = payload;

  let stateInc = [...state];
  let newState = [];
  for (let product of stateInc) {
    if (product.id === id) {
      product.quantity += quantity;
    }
    newState.push(product);
  }
  return newState;
}

const decrementProductQuantity = (state, payload) => {
  let {
    id,
    quantity
  } = payload;
  let stateDec = [...state];
  let newState = [];
  for (let product of stateDec) {
    if (product.id === id && product.quantity - quantity > 0) {
      product.quantity -= quantity;
    }
    newState.push(product);
  }
  return newState;
}

const toggleMustHave = (state, payload) => {
  let newState = [];
  for (let product of state) {
    if (product.item.id === payload) {
      product.mostWanted = !product.mostWanted
    }
    newState.push(product);
  }
  return newState;
}

const addProductToCart = (state, payload) => {
  for (let product of state) {
    if (product.item.id === payload.id) {
      return state
    }
  }

  let product = {}
  product['id'] = payload.id;
  product['isGroupGift'] = false;
  product['isReserved'] = false;
  product['quantity'] = payload.quantity;
  product['mostWanted'] = false;
  product['orderNo'] = null;
  product['giftMessage'] = null;
  product['isAmountReceived'] = false;
  product['isPurchased'] = false;
  product['featuredOrder'] = state.length;

  // TODO: Make item_id dynamic after calling POST API for adding it
  let item = {}
  item['id'] = payload.id;
  item['title'] = payload.title;
  item['shopifyItemId'] = product['id'];
  item['note'] = null;
  item['price'] = payload.variants.edges[0].node.price;

  // TODO: Make category dynamic after POST API call
  let category = {}
  category['id'] = 2;
  category['categoryName'] = 'Bassinet';
  category['isRoot'] = false;

  item['category'] = category;
  product['item'] = item;


  return [...state, product];
}


const setParentFilter = (state, payload) => {
  let newState = {...state}
  newState.parentFilter = payload
  newState.childFilter = [...state.childFilter]

  return newState
}


const setChildFilter = (state, payload) => {
  let newState = {...state}
  newState.childFilter = [...payload]

  return newState
}


// Reducers

// const products = (state = defaultData.myRegistry.defaultProduct, action) => {
const recommendedProducts = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.ADD_RECOMMENDED_PRODUCT:
      return state.length === 0 ? [...action.payload] : [...state, ...action.payload];
    case MY_REGISTRY_ACTION_TYPES.INCREMENT_QUANTITY:
      return incrementProductQuantity(state, action.payload);
    case MY_REGISTRY_ACTION_TYPES.DECREMENT_QUANTITY:
      return decrementProductQuantity(state, action.payload);
    default:
      return state;
  }
}

// const addedProducts = (state = defaultData.myRegistry.defaultAddedProduct, action) => {
const addedProducts = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_REGISTRY_PRODUCTS:
      return action.payload;
    case MY_REGISTRY_ACTION_TYPES.TOGGLE_MUST_HAVE_PRODUCT:
      return toggleMustHave([...state], action.payload);
    case MY_REGISTRY_ACTION_TYPES.ADD_PRODUCT_TO_CART:
      return addProductToCart([...state], action.payload);
    case MY_REGISTRY_ACTION_TYPES.REMOVE_PRODUCT_FROM_CART:
      return state.filter((product, _) => {
        return product.item.id !== action.payload
      })
    default:
      return state;
  }
}

const activeRecommandation = (state = "", action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_ACTIVE_RECOMMANDATION:
      return action.payload;
    default:
      return state;
  }
}

const filteredProducts = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_FILTERED_PRODUCTS:
      return [...action.payload]
    default:
      return state
  }
}

const filterCategory = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_FILTER_CATEGORY:
      return action.payload
    default:
      return state
  }
}

const filterApplied = (state = defaultData.myRegistry.defaultFilter, action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_PARENT_FILTER:
      return setParentFilter(state, action.payload)
    case MY_REGISTRY_ACTION_TYPES.SET_CHILD_FILTER:
      return setChildFilter(state, action.payload)
    case MY_REGISTRY_ACTION_TYPES.CLEAR_FILTER:
      return defaultData.myRegistry.defaultFilter
    default:
      return state;
  }
}

const checkList = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.GET_CHECKLIST_ITEMS:
      return action.payload;
    default:
      return state;
  }
}

const anotherStoreProduct = (state = defaultData.myRegistry.defaultAnotherStoreProductInfo, action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_ANOTHER_STORE_PRODUCT_INFO:
      return {...action.payload};
    case MY_REGISTRY_ACTION_TYPES.CLEAR_ANOTHER_STORE_PRODUCT_INFO:
      return defaultData.myRegistry.defaultAnotherStoreProductInfo;
    default:
      return state;
  }
}

const anotherStoreRegistry = (state = [], action) => {
  switch (action.type) {
    case MY_REGISTRY_ACTION_TYPES.SET_ANOTHER_STORE_REGISTRY_ITEMS:
      return [...action.payload];
    default:
      return state;
  }
}

export default combineReducers({
  recommendedProducts,
  addedProducts,
  activeRecommandation,
  filterCategory,
  filteredProducts,
  filterApplied,
  checkList,
  anotherStoreProduct,
  anotherStoreRegistry
})