import React from 'react'
import "./Counter.scss";

const Counter = (props) => {
	return (
		<div className={["counter", "d-flex", props.className ? props.className : ""].join(" ")}>
			<button className="icon-btn icon-circle-minus pl-0" onClick={props.decrementHandler}></button>
			<span className="mx-2">{props.counter}</span>
			<button className="icon-btn icon-circle-plus pr-0" onClick={props.incrementHandler}></button>
		</div >
	);
}

export default Counter;