export const REGISTRY_ACTION_TYPES = {
  UPDATE_REGISTRY_INFO: "UPDATE_REGISTRY_INFO",
  SET_LOGGING_STATUS: "SET_LOGGING_STATUS"
}

export const updateRegistryInfo = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.UPDATE_REGISTRY_INFO,
    payload: params
  }
}

export const setLoggingStatus = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_LOGGING_STATUS,
    payload: params
  }
}