export const ONBOARDING_ACTION_TYPES = {
  ADD_QUESTION: "ADD_QUESTION",
  ADD_ANSWER: "ADD_ANSWER",
  UPDATE_ANSWER: "UPDATE_ANSWER"
}

export const addOnboardingQuestion = (params) => {
  return {
    type: ONBOARDING_ACTION_TYPES.ADD_QUESTION,
    payload: params
  }
}

export const addQuestionAnswer = (params) => {
  return {
    type: ONBOARDING_ACTION_TYPES.ADD_ANSWER,
    payload: params
  }
}

export const updateQuestionAnswer = (params) => {
  return {
    type: ONBOARDING_ACTION_TYPES.UPDATE_ANSWER,
    payload: params
  }
}