import {ONBOARDING_ACTION_TYPES} from "./OnboardingAction";
import {combineReducers} from "redux";


const questions = (state = [], action) => {
  switch (action.type) {
    case ONBOARDING_ACTION_TYPES.ADD_QUESTION:
      return [...state, ...action.payload]
    default:
      return state
  }
}

const answers = (state = [], action) => {
  switch (action.type) {
    case ONBOARDING_ACTION_TYPES.ADD_ANSWER:
      return [...state, action.payload]
    case ONBOARDING_ACTION_TYPES.UPDATE_ANSWER:
      return [...action.payload]
    default:
      return state
  }
}

export default combineReducers({
  questions,
  answers
});