export const MY_REGISTRY_ACTION_TYPES = {
	// Recommended Product
	ADD_RECOMMENDED_PRODUCT: "ADD_RECOMMENDED_PRODUCT",
	INCREMENT_QUANTITY: "INCREMENT_QUANTITY",
	DECREMENT_QUANTITY: "DECREMENT_QUANTITY",

	// Registry product
	SET_REGISTRY_PRODUCTS: "SET_REGISTRY_PRODUCTS",
	TOGGLE_MUST_HAVE_PRODUCT: "TOGGLE_MUST_HAVE_PRODUCT",
	ADD_PRODUCT_TO_CART: "ADD_PRODUCT_TO_CART",
	REMOVE_PRODUCT_FROM_CART: "REMOVE_PRODUCT_FROM_CART",

	// CheckList
	SET_ACTIVE_RECOMMANDATION : "SET_ACTIVE_RECOMMANDATION",

	// Filter Category
	SET_FILTER_CATEGORY: "SET_FILTER_CATEGORY",

	// Filter
	SET_PARENT_FILTER: "SET_PARENT_FILTER",
	SET_CHILD_FILTER: "SET_CHILD_FILTER",
	CLEAR_FILTER: "CLEAR_FILTER",

	// Filtered Products
	SET_FILTERED_PRODUCTS : "SET_FILTERED_PRODUCTS",
	GET_CHECKLIST_ITEMS: "GET_CHECKLIST_ITEMS",

  //ANOTHER STORE PRODUCT
  SET_ANOTHER_STORE_PRODUCT_INFO : "SET_ANOTHER_STORE_PRODUCT_INFO",
  CLEAR_ANOTHER_STORE_PRODUCT_INFO : "CLEAR_ANOTHER_STORE_PRODUCT_INFO",

  // ANOTHER STORE REGISTRY
  SET_ANOTHER_STORE_REGISTRY_ITEMS: "SET_ANOTHER_STORE_REGISTRY_ITEMS"
}

export const addRecommendedProduct = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.ADD_RECOMMENDED_PRODUCT,
		payload: params
	}
}

export const incrementQuantity = (id, quantity) => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.INCREMENT_QUANTITY,
		payload: { id, quantity }
	}
}

export const decrementQuantity = (id, quantity) => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.DECREMENT_QUANTITY,
		payload: { id, quantity }
	}
}

export const toggleMustHaveProduct = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.TOGGLE_MUST_HAVE_PRODUCT,
		payload: params
	}
}

export const setRegistryProducts = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_REGISTRY_PRODUCTS,
		payload: params
	}
}

export const addProductToCart = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.ADD_PRODUCT_TO_CART,
		payload: params
	}
}

export const removeProduct = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.REMOVE_PRODUCT,
		payload: params
	}
}

export const removeProductFromCart = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.REMOVE_PRODUCT_FROM_CART,
		payload: params
	}
}

export const setActiveRecommandation = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_ACTIVE_RECOMMANDATION,
		payload: params
	}
}

// Filter Category Section
export const setFilterCategory = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_FILTER_CATEGORY,
		payload: params
	}
}

// Filter Section

export const setParentFilter = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_PARENT_FILTER,
		payload: params
	}
}

export const setChildFilter = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_CHILD_FILTER,
		payload: params
	}
}

export const clearFilter = () => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.CLEAR_FILTER,
	}
}

// Filtered Products
export const setFilteredProduct = (params) => {
  return {
    type: MY_REGISTRY_ACTION_TYPES.SET_FILTERED_PRODUCTS,
    payload: params
  }
}

export const setCheckListItems = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.GET_CHECKLIST_ITEMS,
		payload: params
	}
}

// Another store product info
export const setAnotherStoreProductInfo = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_ANOTHER_STORE_PRODUCT_INFO,
		payload: params
	}
}

export const clearAnotherStoreProductInfo = () => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.CLEAR_ANOTHER_STORE_PRODUCT_INFO,
	}
}

//Another store Registry Items
export const setAnotherStoreRegistryItems = params => {
	return {
		type: MY_REGISTRY_ACTION_TYPES.SET_ANOTHER_STORE_REGISTRY_ITEMS,
		payload: params
	}
}