import React, { useEffect } from "react";
import ViewRegistryHeader from "../view-registry-header/ViewRegistryHeader";
import AddedCard from "modules/commons/components/added-card/AddedCard";
import { toggleMustHaveProduct, removeProductFromCartHandler, getProducts, updateViewRegistryPosition } from "utils/commonFunction";
import { useSelector, useDispatch } from 'react-redux';
import SortFilterSection from '../../components/sort-filter-section/SortFilterSection';
import { setViewRegistryProducts, setFeaturedSortApplied } from '../../ViewRegistryActions';
import {
  sortableContainer,
  sortableElement,
} from 'react-sortable-hoc';
import arrayMove from 'array-move';
import './ViewRegistryHome.scss';

const ViewRegistryHome = () => {

  const dispatch = useDispatch()
  let stateAddedProducts = useSelector(state => state.myRegistry.addedProducts)
  let stateViewRegistryProducts = useSelector(state => state.viewRegistry.viewRegistryProducts)
  const activeSortFilterState = useSelector(state => state.viewRegistry.activeSortFilter)
  const stateFeaturedSortApplied = useSelector(state => state.viewRegistry.featuredSortApplied)
  const stateRegistryInfo = useSelector(state => state.registry.info)

  const SortableItem = sortableElement(({ card, index }) => (
    <li className="card-list-item">
      <AddedCard
        data={card} key={"addedCard" + index}
        toggleMustHave={() => toggleMustHaveProduct(card, dispatch)}
        draggable={true}
        disabledDrag={card.disabledDrag}
        removeProductFromCartHandler={() => removeProductFromCartHandler(card, stateRegistryInfo.id, dispatch)}
      />
    </li>
  ));

  let onSortEnd = ({ oldIndex, newIndex }) => {
    let itemsOldPosition = arrayMove([...stateViewRegistryProducts], oldIndex, oldIndex)
    let items = arrayMove([...stateViewRegistryProducts], oldIndex, newIndex)
    dispatch(setViewRegistryProducts(items))
    updateViewRegistryPosition(stateRegistryInfo.id, items, itemsOldPosition, dispatch)
    // dispatch(setViewRegistryProducts(items))
  };

  const SortableContainer = sortableContainer(({ children }) => {
    return <ul className="p-0">{children}</ul>;
  });

  const getSortedFilteredProducts = (addedProducts, activeSortFilterState, viewRegistryProducts) => {
    let sortedProducts = [];
    let sortedData = null;
    // let sortedDataByMustHave = sortByMustHave([...stateAddedProducts])

    if (activeSortFilterState.sortId === 1) {
      if (stateRegistryInfo.isFeatured) {
        sortedData = viewRegistryProducts ? [...viewRegistryProducts] : [];
        // sortedData = sortByFeatured([...addedProducts])
      }
      else {
        sortedData = sortByMustHave([...addedProducts])
        if (sortedData.length > 0) {
          let viewRegistryProductsId = sortedData.map((productData, index) => {
            return { 'id': productData.id }
          })
          // dispatch(setViewRegistryProducts(viewRegistryProductsId))
          // dispatch(setFeaturedSortApplied(true))
        }
      }
    }

    else if (activeSortFilterState.sortId === 2) {
      sortedData = sortByLowToHigh([...addedProducts])
    }
    else if (activeSortFilterState.sortId === 3) {
      sortedData = sortByHighToLow([...addedProducts])
    }

    let viewRegistryProductsId = sortedData.map((productData, index) => {
      return { 'id': productData.id }
    })

    for (let product of viewRegistryProductsId) {
      for (let addProduct of addedProducts) {
        if (product.id === addProduct.id) {
          sortedProducts.push(addProduct);
          break;
        }
      }
    }
    return sortedProducts;
  }

  const sortByMustHave = (productList) => {
    return productList.sort(function (a, b) { return b.mostWanted - a.mostWanted });
  }

  const sortByFeatured = (productList) => {
    return productList.sort(function (a, b) { return a.isFeatured - b.isFeatured });

  }

  const sortByLowToHigh = (productList) => {
    // TODO
    // let mustHaveProducts = [...productList]
    // let index = -1;
    // for (let product of mustHaveProducts) {
    //   index++;
    //   if (product.mostWanted === false) {
    //     break
    //   }
    // }
    // let nonMustHaveProducts = mustHaveProducts.splice(index);
    // let tempMustHaveList = mustHaveProducts.sort((a, b) => { return a.item.price - b.item.price });
    // let tempList = nonMustHaveProducts.sort((a, b) => { return a.item.price - b.item.price })
    // let finalSortedProducts = tempMustHaveList.concat(tempList)
    return productList.sort((a, b) => { return a.item.price - b.item.price });
  }

  const sortByHighToLow = (productList) => {
    // TODO
    // let mustHaveProducts = [...productList]
    // let index = -1;
    // for (let product of mustHaveProducts) {
    //   index++;
    //   if (product.mostWanted === false) {
    //     break
    //   }
    // }
    // let nonMustHaveProducts = mustHaveProducts.splice(index);
    // let tempMustHaveList = mustHaveProducts.sort((a, b) => { return b.item.price - a.item.price });
    // let tempList = nonMustHaveProducts.sort((a, b) => { return b.item.price - a.item.price })
    // let finalSortedProducts = tempMustHaveList.concat(tempList)
    return productList.sort((a, b) => { return b.item.price - a.item.price });
  }

  useEffect(() => {
    if (stateAddedProducts.length === 0) {
      getProducts(stateRegistryInfo.id, dispatch);
    }
    if (stateAddedProducts.length > 0 && stateViewRegistryProducts.length === 0) {
      let sortedData = sortByFeatured([...stateAddedProducts])
      let viewRegistryProducts = sortedData.map((productData, index) => {
        return { 'id': productData.id }
      })
      dispatch(setViewRegistryProducts(viewRegistryProducts))
    }
  })

  useEffect(() => {
    let sortedData = sortByFeatured([...stateAddedProducts])
    let viewRegistryProducts = sortedData.map((productData, index) => {
      return { 'id': productData.id }
    })
    dispatch(setViewRegistryProducts(viewRegistryProducts))
  }, [stateAddedProducts])

  let filteredSortProducts = getSortedFilteredProducts(stateAddedProducts, activeSortFilterState, stateViewRegistryProducts)

  return (
    <>
      <ViewRegistryHeader />
      <div className="container mt-7">
        <div className="row">
          <div className="col-3"></div>
          <div className="col-9">
            {/* Top Filter Section */}
            <SortFilterSection />

            {/* Added Products */}
            <div className="mt-5">
              <SortableContainer onSortEnd={onSortEnd} helperClass="drag-add-card" axis="y" lockAxis="y" useDragHandle>
                {filteredSortProducts.map((card, index) =>
                  <SortableItem key={`item-${index}`} index={index} card={{ ...card, disabledDrag: activeSortFilterState.sortId !== 1 }} disabled={activeSortFilterState.sortId !== 1} />
                )}
              </SortableContainer>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default ViewRegistryHome;