import React from 'react';
import "./RadioButton.scss";

const RadioButton = (props) => {
  return (
    <label className="radio d-flex align-items-center mb-0">
      <span className="radio__input mr-2">
      <input
        type="radio"
        name={props.name}
        checked={props.checked ? "checked" : ""}
        onChange={props.changeHandler}
        />
      <span className="radio__control"></span>
      </span>
      <span className={["radio__label", props.textClass ? props.textClass : null].join(" ")}>{props.display} {props.count ? "("+props.count+")" : null}</span>
    </label>
  )
}

export default RadioButton;