import React from 'react';
import "./FilterItemChip.scss";

const FilterItemChip = (props) => {
  return (
    <span className={["filter-item-chip px-2 mb-1", props.className ? props.className : ''].join(" ")}><span className="text-sm">{props.text}</span><i className="icon-close ml-1" onClick={props.deleteHandler}></i></span>
  )
}

export default FilterItemChip;