import config from './config';
import { Cookies } from 'react-cookie';


const cookies = new Cookies();
// For axios
export const apiURL = process.env.NODE_ENV === 'production' ? config.PRODUCTION_API_URL : config.LOCAL_API_URL;
const AuthToken = cookies.get('access_token') ? "Token " + cookies.get('access_token') : ""

const axios = require("axios");

const axiosInstance = axios.create({
  baseURL: apiURL,
  // TODO: Change Auth Token dyanamic
  // headers : cookies.get('header')
  headers: { "Authorization": AuthToken }
});

const axiosInstanceNoToken = axios.create({
  baseURL: apiURL
});

export function graphQLNoToken(data) {
  return axiosInstanceNoToken.post("/graphql", data);
}

export function graphQL(data) {
  return axiosInstance.post("/graphql", data);
}

export function getRecommendation(data) {
  return axiosInstance.get("/items", data);
}

export function getScrapedProduct(data) {
  return axiosInstance.post("/items/import_item/", data);
}

export function verifyAccessToken(data) {
  return axiosInstanceNoToken.post("/user/verify/token", data);
}

export function getAnotherRegistryProducts(data) {
  return axiosInstance.post("/registry/import", data);
}

const axiosInstanceMultipart = axios.create({
  baseURL: apiURL,
  headers: {
    "Authorization": AuthToken,
    'Content-Type': 'multipart/form-data'
  }
});

export function graphQLMultipart(data) {
  return axiosInstanceMultipart.post("/graphql", data);
}