import React from "react";
import { Switch, Route } from "react-router-dom";
import route from "utils/routes";
import ViewRegistryHome from "./containers/view-registry-home/ViewRegistryHome";

class ViewRegistry extends React.Component {
    render() {
        return (
            <Switch>
                <Route exact path={route.VIEW_REGISTRY.HOME} component={ViewRegistryHome} />
            </Switch>
        )
    }
}

export default ViewRegistry;