const TOGGLE_MUST_HAVE = `
   mutation ToggleMustHaveProduct($productId: Int!, $mustHaveValue: Boolean! $quantity: Int!) {
    updateRegistryItems(registryItemId:$productId, registryItemData:{
    quantity: $quantity,
    mostWanted:$mustHaveValue,
     }){
      message
    }
   }
`

const ADD_REGISTRY_ITEM = `
   mutation addRegistryItems($registryId:Int!,$input: [StoreInput])
   { addRegistryItems(registryId:$registryId,registryItems: $input) {
     message
     }
   }
`

const UPDATE_REGISTRY = `
  mutation updateRegistryDetail($registryId: Int!, $input: RegistryInput!){
    updateRegistry(registryId: $registryId, registryData: $input) {
      registry{
      name,
      image{
            id,image
        }
      }
    }
  }
`;

const UPDATE_VIEW_REGISTRY_POISTION = `
  mutation registryItemsOrder($registryId:Int!, $registryItems: [RegistryItemOrderInput]){
    registryItemsOrder(registryId: $registryId,registryItems: $registryItems){
      message
    }
  }
`;

const UPDATE_REGISTRY_URL = `
mutation updateRegistryUrl($registryId:Int!, $urlSlug:String!){
  checkUpdateRegistryUrlSlug(registryId:$registryId,urlSlugInput:{
    urlSlug:$urlSlug
  }){
    message,
    registry{
      id
    }
  }
}
`

const REGISTER_USER = `
mutation register_user($name: String!, $email: String!, $password: String!){
  registerUser(registerInput:{
      firstName:$name,
      email:$email,
      password:$password
  }
 ){
    user{
      id
    },
    token,
    redirectionUrl
  }
}
`
const SAVE_QUESTION_RESPONSE = `
mutation addRegistryQuestionOption($response: [RegistryQuestionOptionInput]!){
  addRegistryQuestionOption(questionOptionInput: $response){
    message
  }
}
`

const SAVE_REGISTRY_DATA = `
mutation saveRegistryData($registryData:RegistryInput!, $registryItems:[StoreInput]){
  importRegistry(registryData: $registryData, registryItems: $registryItems) {
    message
  }
}
`

export { TOGGLE_MUST_HAVE, ADD_REGISTRY_ITEM, UPDATE_REGISTRY, UPDATE_VIEW_REGISTRY_POISTION, UPDATE_REGISTRY_URL, SAVE_REGISTRY_DATA, REGISTER_USER, SAVE_QUESTION_RESPONSE };
