import React from 'react';
import "./CheckBox.scss";

const CheckBox = (props) => {
  return (
    <label className="checkbox d-flex align-items-center mb-4">
      <span className="checkbox__input mr-2">
        <input 
          type="checkbox" 
          name={props.id} 
          value={props.id}
          checked={props.checked ? "checked" : ""}
          onChange={props.changeHandler}
          />
        <span className="checkbox__control d-flex justify-content-center align-items-center">
        <i className="icon-check"></i>                          
        </span>
      </span>
      <span className="radio__label text-sm">{props.display} {props.count ? "("+props.count+")" : null}</span>
    </label>
  )
}

export default CheckBox;