import React from 'react';
import "./SortFilter.scss";
import { useSelector, useDispatch } from 'react-redux';
import { activeSortFilter } from '../../ViewRegistryActions';

const SortFilter = () => {

  const dispatch = useDispatch()
  const activeSortFilterState = useSelector(state => state.viewRegistry.activeSortFilter)
  const filterContentRef = React.createRef();

  const toggleFilterContent = () => {
    filterContentRef.current.classList.toggle("show");
  }


  const handleSortSelection = (sortItem) => {
    dispatch(activeSortFilter(sortItem))
    filterContentRef.current.classList.toggle("show");
  }

  const sortByList = [
    {
      sortId: 1,
      sortTitle: 'Featured'
    },
    {
      sortId: 2,
      sortTitle: 'Price: Low to High'
    },
    {
      sortId: 3,
      sortTitle: 'Price: High to Low'
    }
  ]

  return (
    <div className="sort-section-filter">
      <div className="sort-filter-btn px-2 d-flex justify-content-between py-1" onClick={toggleFilterContent}>
        <span className="font-body selected-sort-text text-body">{activeSortFilterState.sortTitle}</span>
        <i className="icon-filter-1 pl-1 mr-1 icon-chevron-down" />
      </div>
      <div className="filter-content" ref={filterContentRef}>
        <div className="d-flex flex-column">
          {sortByList.map(sortItem => {
            return (
              <div className="filter-item py-2 px-2" key={"sortid-" + sortItem.sortId} onClick={() => handleSortSelection(sortItem)}>
                <div className="text-sm">{sortItem.sortTitle}</div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  )
}

export default SortFilter;
