import React, {Component} from "react";
import "./CheckListItems.scss";
import CircularProgress from "modules/commons/components/circular-progress/CircularProgress";
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import {connect} from 'react-redux';

class CheckListItems extends Component {

  constructor(props) {
    super(props);
    // create a ref to store the checkList header DOM element
    this.checkListHeader = React.createRef();
  }

  getOffset = (el) => {
    const rect = el.getBoundingClientRect();
    return {
      left: rect.left + window.scrollX,
      top: rect.top + window.scrollY
    };
  }

  scrollToCategory = (category) => {
    const categorySection = document.getElementById(category)
    if (categorySection) {
      let offset = this.getOffset(categorySection);
      window.scrollTo(0, offset.top)
    }
  }

  getSubList = (getSubList) => {
    let subItems = getSubList.map(({id, categoryName, addedItemsCount, totalItemsCount}) => {
      return (
        <div className="d-flex card-body-contain align-items-center py-2" key={categoryName + id}
             onClick={() => this.scrollToCategory(categoryName)}>
          <p
            className="mb-0 card-body-header text-sm">{categoryName} {totalItemsCount > 1 ? `(${totalItemsCount})` : null}</p>
          <div className='ml-auto mb-0 align-self-end card-progress'>
            {(addedItemsCount >= totalItemsCount) && (totalItemsCount !== 0)?
              <div className="icon-done"/> :
              (totalItemsCount > 1 && addedItemsCount > 0 ?
                <CircularProgress
                  percentage={(addedItemsCount / totalItemsCount) * 100}
                  text={addedItemsCount}
                  className="checklist-circular-progress"
                />
                : null)}
          </div>
        </div>
      )
    });
    return subItems;
  }

  handleToggleEvent = () => {
    const checkListIconEl = this.checkListHeader.current.firstChild;
    checkListIconEl.classList.toggle("closed");
    checkListIconEl.classList.toggle("opened");
  }

  render() {

    let subListItem = this.getSubList(this.props.item.subCategories)
    // TODO: For calculation for subitem
    // let parentTotal = 10;
    // let parentAdded = 16;

    // for (let subItems of this.props.item.subCategories) {
    //     if (subItems.added !== subItems.total) {
    //         parentAdded += Number(subItems.added);
    //         parentTotal += Number(subItems.total);
    //     }
    // }

    return (
      <Accordion className="accordion-list mb-5" key={this.props.item.categoryName}>
        <Card>
          <Accordion.Toggle
            onClick={this.handleToggleEvent}
            as={Card.Header}
            id={this.props.item.categoryName}
            eventKey={this.props.item.categoryName}
          >
            <div ref={this.checkListHeader} className='d-flex icon-checklist'>
              <span className="mr-3 checklist-icon-arrow closed"/>
              <p className="mb-0 body-text">{this.props.item.categoryName}</p>
              <div className='ml-auto mb-auto align-self-end card-progress'>
                {this.props.item.addedItemsCount}/{this.props.item.totalItemsCount}
              </div>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse className=' mt-1 ml-6' eventKey={this.props.item.categoryName}>
            <Card.Body>
              {subListItem}
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    );

  }
}

export default connect()(CheckListItems);