import React from "react";
import "./Onboarding.scss";
import {connect} from "react-redux";
import {Redirect} from "react-router";
import routes from "utils/routes";
import Option from "../commons/components/questions/option/Option";
import {addQuestionAnswer, updateQuestionAnswer} from "./OnboardingAction";
import Button from "../commons/components/button-component/ButtonComponent";
import {toast, ToastContainer} from "react-toastify";
import {REGISTER_USER, SAVE_QUESTION_RESPONSE} from "../../api/mutations";
import {graphQLNoToken, graphQL, apiURL} from "../../api/axios";
import {Cookies} from "react-cookie";
import {setLoggingStatus} from "../registry/RegistryActions";
import config from "../../api/config";
import axios from "axios";

// TODO: Integrate Response AND Login API
class Onboarding extends React.Component {

  // Static titles
  LOGIN_TITLE = "Sign in for free gifts, free shipping, and exclusive access to our limited edition prints"
  SIGNUP_TITLE = "Lets make it official. Time to create your account."
  PARTNER_TITLE = "Your partners details please"

  // Answer type mapping
  RADIO = "Radio"
  LOGIN = "Login"
  PARTNER = "Partner"

  state = {
    parentId: null,
    question: {},
    login: false,
    toastMessage: ""
  }

  componentDidMount() {
    window.onpopstate = () => {
      if (this.props.location.pathname.search(routes.ONBOARDING.HOME) >= 0) {
        this.setQuestionBasedOnId(this.props.match.params['questionId'])
        this.clearToastMessage()
      }
    }
  }

  submitAllAnswers = () => {
    if (this.props.stateOnboardingAnswers.length > 0) {
      const response = this.props.stateOnboardingAnswers.map(answer => {
        return {
          question: answer.question,
          option: answer.response.option,
          optionText: answer.response.optionText
        }
      })

      const apiURL = process.env.NODE_ENV === 'production' ? config.PRODUCTION_API_URL : config.LOCAL_API_URL;
      const cookies = new Cookies();
      let reqData = {
        query: SAVE_QUESTION_RESPONSE,
        operationName: "addRegistryQuestionOption",
        variables: {
          response: response
        }
      }

      axios({
        method: 'post',
        url: apiURL + 'graphql',
        data: reqData,
        headers: { "Authorization": cookies.get("access_token") ? "Token "+cookies.get("access_token") : "" }
      }).then(res => {
        if(res.data.data.addRegistryQuestionOption){
          console.log("Saved");
        }
      }).catch(err => {
        console.error(err);
      })

      // graphQL(reqData).then(res => {
      //   console.log(res.data.data);
      // }).catch(err => {
      //   console.error(err);
      // })
    }
  }

  clearToastMessage = () => {
    this.setState({
      toastMessage: ""
    })
  }

  setToastMessage = () => {
    if (this.state.question.toastMessage) {
      this.setState({
        toastMessage: this.state.question.toastMessage
      })
    } else {
      this.clearToastMessage()
    }
  }

  gotoNextQuestion = (question, parentId, option = {}) => {
    let nextQuestionId = null;
    let returnNext = false;
    const idToMatch = parentId ? parentId : question.id;
    // Only works for 1 follow up question
    if (option.followUpQuestions && option.followUpQuestions.length > 0) {
      nextQuestionId = option.followUpQuestions[0].question.id;
    } else {
      for (let ques of this.props.stateOnboardingQuestions) {
        if (returnNext) {
          nextQuestionId = ques.id
          break
        }
        if (ques.id === idToMatch) {
          returnNext = true;
        }
      }
    }

    if (nextQuestionId === null && returnNext) {
      //  This is last question
      console.log("Last Question");
      if(this.props.stateLoggedIn && this.state.redirectionUrl){
        window.open(this.state.redirectionUrl,"_self")
      }
    } else if (nextQuestionId) {
      this.setState({
        parentId: null
      })

      this.setQuestionBasedOnId(nextQuestionId)
      this.setToastMessage()
      this.props.history.push(routes.ONBOARDING.HOME + `/${nextQuestionId}`)
    }
  }

  setQuestionBasedOnId = (questionId) => {
    if (questionId) {
      let finalQuestion = {};
      let foundQuestion = false;
      let parentId = null;
      for (const question of this.props.stateOnboardingQuestions) {
        if (foundQuestion) {
          break;
        }
        if (question.id === questionId) {
          finalQuestion = question;
          foundQuestion = true;
          break;
        }
        for (const option of question.options) {
          if (option.followUpQuestions.length > 0) {
            for (const followUpQuestion of option.followUpQuestions) {
              if (followUpQuestion.question.id === questionId) {
                finalQuestion = followUpQuestion.question;
                parentId = followUpQuestion.parentQuestion;
                foundQuestion = true
                break;
              }
            }
          }
        }
      }

      if (finalQuestion.id) {
        this.setState({
          question: finalQuestion,
          parentId: parentId
        })
      } else {
        console.log("No Question Found");
      }
    }
  }

  addAnswerToState = (response) => {
    const alreadyAnswered = this.props.stateOnboardingAnswers.find(el => el.question === this.state.question.id)
    if (alreadyAnswered) {
      // This will delete the answers that were given after the matched response
      let newState = []
      for (let answer of this.props.stateOnboardingAnswers) {
        if (answer.question === this.state.question.id) {
          answer.response = response;
          newState.push(answer)
          break;
        }
        newState.push(answer)
      }
      this.props.dispatch(updateQuestionAnswer(newState))
    } else {
      this.props.dispatch(addQuestionAnswer({question: this.state.question.id, response: response}))
    }

    if (this.props.stateLoggedIn) {
      this.submitAllAnswers()
    }
  }

  answerRadioQuestion = (option) => {
    this.addAnswerToState({option: option.id, optionText: option.value});
    this.gotoNextQuestion(this.state.question, this.state.parentId, option);
  }

  watchNextBtn = (ev) => {
    this.setState({
      nextBtnState: ev.target.value.length <= 0
    })
  }

  answerDataQuestion = (response) => {
    this.addAnswerToState({option: 0, optionText: response})
    this.gotoNextQuestion(this.state.question, this.state.parentId);
  }

  textInputTypeChangeHandler = (ev, inputRefs) => {
    let enableBtn = false;

    for (const inputRef of inputRefs) {
      if (inputRef.current.value.length <= 0) {
        enableBtn = true
        break
      }
    }

    this.setState({
      nextBtnState: enableBtn
    })
  }

  changeLoginState = (ev, value) => {
    ev.preventDefault()
    this.setState({
      login: value
    })
  }

  getSignInContent = () => {
    return (
      <>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Email Address</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="email"
            name="email"
            placeholder="Your Email"
          />
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Password</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="password"
            name="password"
            placeholder="Password"

          />
        </div>
        <Button
          variant="primary"
          className="px-3 py-1 font-medium onboarding-next-button"
          onClick={() => this.gotoNextQuestion(this.state.question, this.state.parentId)}
        >Sign In</Button>
        {/*TODO : Add Api on Onclick of the button above*/}
        <h4 className="mt-11">Don't have an account? <a href="#" className="font-medium onboarding-link-text"
                                                        onClick={(ev) => this.changeLoginState(false)}>Sign
          Up</a></h4>
      </>
    )
  }

  signUpClickHandler = (nameRef, emailRef, passwordRef) => {
    if (nameRef.current.value.length <= 0 || emailRef.current.value.length <= 0 || passwordRef.current.value.length <= 0) {
      alert("Please enter all fields")
      return;
    }

    let reqData = {
      query: REGISTER_USER,
      operationName: "register_user",
      variables: {
        name: nameRef.current.value,
        email: emailRef.current.value,
        password: passwordRef.current.value
      }
    }

    graphQLNoToken(reqData).then(res => {
      if (res.data.data.registerUser) {
        const cookies = new Cookies();
        cookies.set("access_token", res.data.data.registerUser.token, {path: "/"})
        this.setState({
          redirectionUrl: res.data.data.registerUser.redirectionUrl
        })
        this.props.dispatch(setLoggingStatus(true))
        this.submitAllAnswers()
      }
    }).catch(err => {
      console.error(err);
    })
  }

  getSignUpContent = () => {
    const nameRef = React.createRef()
    const emailRef = React.createRef()
    const passwordRef = React.createRef()
    return (
      <>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Name</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="text"
            name="name"
            placeholder="Name"
            ref={nameRef}
          />
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Email Address</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="email"
            name="email"
            placeholder="Your Email"
            ref={emailRef}
          />
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Password</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="password"
            name="password"
            placeholder="Password"
            ref={passwordRef}

          />
        </div>
        <Button
          variant="primary"
          className="px-3 py-1 font-medium onboarding-next-button"
          onClick={() => this.signUpClickHandler(nameRef, emailRef, passwordRef)}
        >Sign Up</Button>
        {/*TODO : Add Api on Onclick of the button above*/}
        <h4 className="mt-11">Already have an account? <a href="#" className="font-medium onboarding-link-text"
                                                          onClick={(ev) => this.changeLoginState(ev, true)}>Sign
          In</a></h4>
      </>
    )
  }

  getPartnerContent = () => {
    const inputRefs = [React.createRef(), React.createRef()]
    return (
      <>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Name</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="text"
            name="name"
            placeholder="Name"
            ref={inputRefs[0]}
            onChange={(ev) => this.textInputTypeChangeHandler(ev, inputRefs)}
          />
        </div>
        <div className="d-flex flex-column mb-4">
          <span className="text-sm font-medium onboarding-required-field">Email Address</span>
          <input
            className="onboarding-input py-1 px-2 text-body mt-1"
            type="email"
            name="email"
            placeholder="Your Email"
            ref={inputRefs[1]}
            onChange={(ev) => this.textInputTypeChangeHandler(ev, inputRefs)}
          />
        </div>
        <Button
          variant="primary"
          disabled={this.state.nextBtnState !== undefined ? this.state.nextBtnState : true}
          className="px-3 py-1 font-medium onboarding-next-button"
          onClick={() => this.gotoNextQuestion(this.state.question, this.state.parentId)}
        >Next</Button>
        {/*TODO : Add Api on Onclick of the button above*/}
      </>
    )
  }

  render() {

    // Redirecting user to registry home if registry found
    if (this.props.stateRegistryInfo.id) {
      return <Redirect to={routes.MY_REGISTRY.HOME}/>
    }

    // Redirecting to first question if not answered any
    if (this.props.stateOnboardingQuestions.length > 0
      && this.props.stateOnboardingAnswers.length <= 0
      && this.props.location.pathname !== routes.ONBOARDING.HOME + `/${this.props.stateOnboardingQuestions[0].id}`) {
      return <Redirect to={routes.ONBOARDING.HOME + `/${this.props.stateOnboardingQuestions[0].id}`}/>
    }

    if (!this.state.question.title && this.props.stateOnboardingQuestions.length > 0 && this.props.stateRouter.location.pathname !== routes.ONBOARDING.HOME + `/${this.state.question.id}`) {
      this.setQuestionBasedOnId(this.props.match.params['questionId'])
    }

    // Skipping Login Question if user is already logged in
    if (this.props.stateLoggedIn && this.state.question.answerInputType === this.LOGIN) {
      this.gotoNextQuestion(this.state.question, null);
    }

    if (this.state.toastMessage) {
      toast.dismiss()
      toast.info(<h4 className="font-medium text-center">{this.state.toastMessage}</h4>)
    } else {
      toast.dismiss()
    }

    // Setting all the options for the questions
    let options = []
    let staticQuestionTitle = "";

    if (this.state.question.answerInputType === this.RADIO) {
      options = this.state.question.options.map(option => (
        <Option className="mb-2 mr-4" onClick={() => this.answerRadioQuestion(option)}>
          <h4 className="d-inline">{option.value}</h4>{option.helpText ?
          <span className="text-sm ml-1 onboarding-option-help-text">{option.helpText}</span> : null}</Option>
      ))
    } else if (this.state.question.answerInputType === "Date") {
      let inputRef = React.createRef();
      options.push(
        <div className="d-flex flex-column">
          {/*TODO: Get placeholder from backend*/}
          <input
            type="date"
            className="onboarding-input py-1 px-2 text-body"
            placeholder="Date"
            ref={inputRef}
            onChange={(ev) => this.watchNextBtn(ev)}
          />
          <Button
            variant="primary"
            disabled={this.state.nextBtnState !== undefined ? this.state.nextBtnState : true}
            onClick={() => this.answerDataQuestion(inputRef.current.value)}
            className="px-3 py-1 font-medium mt-4 onboarding-next-button"
          >Next</Button>
        </div>
      )
    } else if (this.state.question.answerInputType === "Text") {
      let inputRefs = []
      options.push(
        // TODO: Update this part if there is a Text type question
        <div className="d-flex flex-column">
          {JSON.parse(this.state.question.fields).map(field => {
            const inputRef = React.createRef()
            inputRefs.push(inputRef)
            return (
              <div className="d-flex flex-column mb-4">
                <span className="text-sm font-medium onboarding-required-field">{field.title}</span>
                <input
                  className="onboarding-input py-1 px-2 text-body mt-1"
                  type={field.type}
                  name={field.name}
                  placeholder={field.title}
                  ref={inputRef}
                  onChange={(ev) => this.textInputTypeChangeHandler(ev, inputRefs)}
                />
              </div>
            )
          })}
          <Button
            variant="primary"
            disabled={this.state.nextBtnState !== undefined ? this.state.nextBtnState : true}
            className="px-3 py-1 font-medium onboarding-next-button"
          >Next</Button>
        </div>
      )
    } else if (this.state.question.answerInputType === this.LOGIN) {
      let content = null;
      if (this.state.login) {
        staticQuestionTitle = this.LOGIN_TITLE
        content = this.getSignInContent()
      } else {
        staticQuestionTitle = this.SIGNUP_TITLE
        content = this.getSignUpContent()
      }

      options.push(content)
    } else if (this.state.question.answerInputType === this.PARTNER) {
      staticQuestionTitle = this.PARTNER_TITLE;
      options.push(this.getPartnerContent())
    }

    return (
      <>
        <div className="baby-registry-onboarding container">
          <div className="row justify-content-center mt-24">
            <div className="col-12 col-md-6">
            <span
              className="font-body onboading-question">{[this.LOGIN, this.PARTNER].indexOf(this.state.question.answerInputType) !== -1 ? staticQuestionTitle : this.state.question.title}</span>
            </div>
          </div>
          <div className="row justify-content-center mt-6">
            <div className="col-12 col-md-6">
              {options.map(option => option)}
            </div>
          </div>
        </div>
        <ToastContainer
          position="top-center"
          className="onboarding-toast-container"
          toastClassName="onboarding-toast px-3 py-1"
          autoClose={4000}
          hideProgressBar={true}
          newestOnTop={true}
          draggable={false}
          closeOnClick={false}
          closeButton={null}
        />
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
    stateLoggedIn: state.registry.loggedIn,
    stateOnboardingQuestions: state.onboarding.questions,
    stateOnboardingAnswers: state.onboarding.answers,
    stateRouter: state.router
  }
}

export default connect(
  mapStateToProps
)(Onboarding);