import React from "react";
import "./DefaultImage.scss";

const DefaultImage = (props) => {
  return (
    <div className="default-image-card" data-selected={props.selected} onClick={props.onClick}>
      <img src={props.src} alt="Default"/>
      {props.selected ? <i className="icon-check selected-image p-1"/> : null}
    </div>
  )
}

export default DefaultImage;