import axios from 'axios';
import { push } from "connected-react-router";
import { API_END_POINTS, API_URL } from '../../../utils/constants'

export const VERIFY_TOKEN_ACTION_TYPES = {
    VERIFY_TOKEN: "VERIFY_TOKEN",
    SET_CUSTOMER: "SET_CUSTOMER"
}

export const setCustomer = (payload) => {
    return {
        type: VERIFY_TOKEN_ACTION_TYPES.SET_CUSTOMER,
        payload: payload
    }
}

export const verifyTokenRequest = params => {
    return dispatch => {
        axios.post(API_URL + API_END_POINTS.VERIFY_TOKEN, {
            token: params.token
        }).then(response => {
            dispatch(setCustomer({
                customer: response.data.data.customer,
                token: response.data.data.token
            }))
        }).then(() => {
            dispatch(redirectVerifyToken())
        }).catch(error => {
            console.log(error);
        })

    }
}

export const redirectVerifyToken = () => dispatch => {
    dispatch(push("/another"));
};